import { Component} from '@angular/core';
import { BeerService } from './beer.service';
import { Beer } from './beer.model';

@Component({
    selector: 'app-root',
    template: require('./app.component.html'),
    providers: [BeerService]
})

export class AppComponent {

    message = 'This is the sample message.';
    title: string = "test";
    randombeer: Beer;

    constructor(private _beerService: BeerService) {}

    ngOnInit() {
        this._beerService.getRandomBeer()
            .subscribe(
                // Success
                response => {
                    console.log(response.data.name);
                    console.log(response.data.description);
                    this.randombeer.name = response.data.name;
                    this.randombeer.description = response.data.description;
                    this.randombeer.category = response.data.style.category.name;
                    this.randombeer.abv = response.data.abv;
                    this.randombeer.img = response.data.labels.large;
                    this.randombeer.shortname = response.data.style.shortName;
                    this.randombeer.id = response.data.id;
                },
                // Failure
                response => {
                    // Do something
                    console.log(response);
                }
            );
    }
}


